nav{width:auto; float:right;}
nav ul{display:table; float:right;}
nav ul li{float:left;}
nav ul li:last-child{padding-right:0px;}
nav ul li div{color:#002e5b; font-size:18px; padding: 25px 20px; display:inline-block; -webkit-transition: all 0.5s ease 0s; transition: all 0.5s ease 0s;}
