nav ul li div:hover i{color:#fde428; -webkit-transition: all 0.5s ease 0s; transition: all 0.5s ease 0s;}
nav ul li div i{padding-right:10px; color:#002e5b; -webkit-transition: all 0.5s ease 0s; transition: all 0.5s ease 0s;}
.toggle-menu ul{display:table; width:25px;}
.toggle-menu ul li{width:100%; height:3px; background-color:#002e5b; margin-bottom:4px;}
.toggle-menu ul li:last-child{margin-bottom:0px;}
